import Slider from "react-slick";

function MainBotSlider() {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
  };
  return (
    <div className="botSlider">
      <h3 className="botSlider-header">
        <img
          src="https://cdn.mrpizza.co.kr/2014_resources/images/main/bg_main_tit.png"
          alt="   "
          className="botSlider-img"
        />
        <span className="botSlider-text"> </span>
      </h3>
      <div className="botSlider-area">
        <Slider {...settings}>
          <div className="botSlider-slide">
            <div className="botSlider-slide-mt">
              <a href="#">
                <span> </span>
                <br />
                <strong>20%</strong>
              </a>
            </div>
          </div>
          <div className="botSlider-slide">
            <div className="botSlider-slide-mt">
              <a href="#">
                <span> </span>
                <br />
                <strong>20%</strong>
              </a>
            </div>
          </div>
          <div className="botSlider-slide">
            <div className="botSlider-slide-mt">
              <a href="#">
                <span></span>
                <br />
                <strong>20%</strong>
              </a>
            </div>
          </div>
          <div className="botSlider-slide">
            <div className="botSlider-slide-mt">
              <a href="#">
                <span> </span>
                <br />
                <strong>30%</strong>
              </a>
            </div>
          </div>
          <div className="botSlider-slide">
            <div className="botSlider-slide-mt">
              <a href="#">
                <strong className="group_order">
                  
                  <br />
                  
                </strong>
              </a>
            </div>
          </div>
        </Slider>
      </div>
    </div>
  );
}

export default MainBotSlider;
