import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";

const Store = ({ stores, getDistanceFromLatLonInKm }) => {
  const [storeInfo, setStoreInfo] = useState({});
  const logindUser = JSON.parse(sessionStorage.getItem("logedInUser"));
  const location = useLocation();
  const hashName = location.hash;
  const aaa = document.querySelector('select[name="select-address1"]');
  const bbb = document.querySelector('select[name="select-address2"]');

  useEffect(() => {
    if (hashName === "#FindStore") {
      const storeNameElement = document.querySelector(".result-store-strong");

      const kakaoScript = document.createElement("script");
      kakaoScript.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=89f833dcfaa9b766dfea38b685368114&autoload=false&libraries=services`;
      kakaoScript.async = true;

      //     
      kakaoScript.onload = () => {
        console.log("Kakao  API   .");
        if (window.kakao && window.kakao.maps) {
          window.kakao.maps.load(() => {
            const mapContainer = document.getElementById("map"); //   div
            const mapOption = {
              center: new window.kakao.maps.LatLng(33.450701, 126.570667), //  
              level: 3, //   
            };

            const map = new window.kakao.maps.Map(mapContainer, mapOption);

            if (
              logindUser &&
              logindUser.MEMBER_ADRESS &&
              logindUser != undefined
            ) {
              // -   
              const geocoder = new window.kakao.maps.services.Geocoder();

              //    
              geocoder.addressSearch(
                logindUser.MEMBER_ADRESS,
                (result, status) => {
                  if (status === window.kakao.maps.services.Status.OK) {
                    const userCoords = new window.kakao.maps.LatLng(
                      result[0].y,
                      result[0].x
                    );

                    //      
                    let nearestStore = null;
                    let nearestDistance = Infinity;

                    stores.forEach((store) => {
                      const distance = getDistanceFromLatLonInKm(
                        result[0].y,
                        result[0].x,
                        store.lat,
                        store.lng
                      );
                      if (distance < nearestDistance) {
                        nearestDistance = distance;
                        nearestStore = store;
                      }
                    });

                    if (nearestStore) {
                      const nearestCoords = new window.kakao.maps.LatLng(
                        nearestStore.lat,
                        nearestStore.lng
                      );

                      //     
                      var marker = new window.kakao.maps.Marker({
                        map: map,
                        position: nearestCoords,
                      });

                      //      
                      map.setCenter(nearestCoords);

                      var iwContent = `<div style="padding:5px; font-size:12px; width:200px;">${nearestStore.name}<br>${nearestStore.tel}<br>${nearestStore.address}</div>`; //    HTML  document element 
                      var iwPosition = new window.kakao.maps.LatLng(
                        nearestCoords
                      ); //  

                      //  
                      var infowindow = new window.kakao.maps.InfoWindow({
                        position: iwPosition,
                        content: iwContent,
                      });

                      //    .   marker     
                      infowindow.open(map, marker);

                      storeNameElement.textContent = nearestStore.name;
                      setStoreInfo({
                        name: " " + nearestStore.name,
                        tel: nearestStore.tel,
                        address: nearestStore.address,
                      });
                    }
                  } else {
                    alert("    .");
                  }
                }
              );
            } else {
              //    
              const coords = new window.kakao.maps.LatLng(
                stores[0].lat,
                stores[0].lng
              );

              var marker = new window.kakao.maps.Marker({
                map: map,
                position: coords,
              });
              map.setCenter(coords);
              storeNameElement.textContent = stores[0].name;

              var iwContent = `<div style="padding:5px; font-size:12px; width:200px;">${stores[0].name}<br>${stores[0].tel}<br>${stores[0].address}</div>`; //    HTML  document element 
              var iwPosition = new window.kakao.maps.LatLng(coords); //  

              //  
              var infowindow = new window.kakao.maps.InfoWindow({
                position: iwPosition,
                content: iwContent,
              });

              //    .   marker     
              infowindow.open(map, marker);
              setStoreInfo({
                name: " " + stores[0].name,
                tel: stores[0].tel,
                address: stores[0].address,
              });
            }
          });
        } else {
          console.error("Kakao API   .");
        }
      };

      kakaoScript.onerror = () => {
        console.error("Kakao  API   .");
      };

      document.head.appendChild(kakaoScript);
    }
  }, [hashName]);

  function searchStore() {
    const storeNameElement = document.querySelector(".result-store-strong");
    const ask = document.getElementById("search-store").value;

    if (ask !== "" && ask !== undefined && ask !== null) {
      if (ask.length >= 2) {
        const kakaoScript = document.createElement("script");
        kakaoScript.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=89f833dcfaa9b766dfea38b685368114&autoload=false&libraries=services`;
        kakaoScript.async = true;

        const filteredStores = stores.filter((store) =>
          store.name.includes(ask)
        );

        if (filteredStores.length > 0) {
          const resultStore = filteredStores[0]; //    
          const latLng = new window.kakao.maps.LatLng(
            resultStore.lat,
            resultStore.lng
          );

          const mapContainer = document.getElementById("map"); //   div
          const mapOption = {
            center: latLng, //      
            level: 3, //   
          };

          const map = new window.kakao.maps.Map(mapContainer, mapOption);

          const marker = new window.kakao.maps.Marker({
            map: map,
            position: latLng,
          });

          //      
          map.setCenter(latLng);

          const iwContent = `<div style="padding:5px; font-size:12px; width:200px;">${resultStore.name}<br>${resultStore.tel}<br>${resultStore.address}</div>`; //   
          const infowindow = new window.kakao.maps.InfoWindow({
            position: latLng, //  
            content: iwContent,
          });

          //    .
          infowindow.open(map, marker);
          storeNameElement.textContent = filteredStores[0].name;
          setStoreInfo({
            name: " " + filteredStores[0].name,
            tel: filteredStores[0].tel,
            address: filteredStores[0].address,
          });
          console.log(aaa);
          aaa.value = "";
          bbb.value = "";
        } else {
          alert("  .");
        }
      } else {
        alert(" 2  ");
      }
    } else {
      alert(" ");
    }
  }

  return (
    <div id="container">
      <aside id="lnb">
        <h1></h1>
        <ul>
          <li>
            <a
              href="#FindStore"
              title=""
              className={hashName === "#FindStore" ? "on" : null}
            >
              
            </a>
          </li>
          <li>
            <a href="#buffet" className={hashName === "#buffet" ? "on" : null}>
              
            </a>
          </li>
        </ul>
      </aside>
      <section id="join-contents">
        <ul id="location">
          <li>
            <a className="home" href="/">
              HOME
            </a>
          </li>
          <li>
            <span></span>
          </li>
          <li>
            <strong></strong>
          </li>
        </ul>

        <div className="cont_top">
          {hashName === "#FindStore" ? <h1></h1> : <h1></h1>}
        </div>
        {hashName === "#FindStore" ? (
          <div className="search-cont">
            <div className="search-area">
              <input
                type="text"
                className="search-store"
                id="search-store"
                placeholder=" "
              />
              <button
                type="button"
                className="button"
                style={{
                  marginLeft: "10px",
                  height: "30px",
                  marginBottom: "10px",
                }}
                onClick={searchStore}
              >
                
              </button>
              <div className="select-address-area">
                <select name="select-address1" className="select-address1">
                  <option value="">/</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
                <select name="select-address2" className="select-address2">
                  <option value="">//</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
            </div>
            <div className="result-store">
              <strong className="result-store-strong"></strong>
            </div>

            <div className="map-area">
              <div id="map" style={{ width: "100%", height: "350px" }}></div>
            </div>
            <div className="store-info">
              <div className="f_left">
                <h2 className="store-name" id="store-name">
                  {storeInfo.name}
                </h2>
                <table className="st_tbl_style">
                  <colgroup>
                    <col style={{ width: "83px" }} />
                    <col />
                  </colgroup>
                  <tbody>
                    <tr>
                      <th scope="row"></th>
                      <td className="t_left branch_tel1 branch_data">
                        {storeInfo.tel}
                      </td>
                    </tr>
                    <tr>
                      <th scope="row"></th>
                      <td className="t_left branch_addr_doro branch_data">
                        {storeInfo.address}
                      </td>
                    </tr>

                    <tr>
                      <th scope="row"></th>
                      <td className="t_left">
                        <table className="tbl_style2">
                          <colgroup>
                            <col />
                            <col style={{ width: "30%" }} />
                            <col style={{ width: "30%" }} />
                            <col style={{ width: "30%" }} />
                          </colgroup>
                          <thead>
                            <tr>
                              <th scope="col" className="bggy"></th>
                              <th scope="col" className="bggy">
                                <strong></strong>
                              </th>

                              <th scope="col" className="bggy">
                                <strong></strong>
                              </th>
                              <th scope="col" className="bggy">
                                <strong></strong>
                              </th>
                            </tr>
                            <tr>
                              <th scope="col"></th>
                              <td> </td>
                              <td>11:00 ~ 21:30</td>
                              <td>11:00 ~ 21:30</td>
                            </tr>
                            <tr>
                              <th scope="col"></th>
                              <td> </td>
                              <td>11:00 ~ 21:30</td>
                              <td>11:00 ~ 21:30</td>
                            </tr>
                          </thead>
                          <tbody></tbody>
                        </table>
                      </td>
                    </tr>
                    <tr>
                      <th scope="row"></th>
                      <td className="t_left branch_info">
                               .
                      </td>
                    </tr>
                    <tr>
                      <th scope="row"></th>
                      <td className="t_left store_ico">
                        <img
                          className="ico "
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_packing2.gif"
                          alt="  20% "
                        />
                        <img
                          className="ico hide"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_packing.gif"
                          alt="  30% "
                        />
                        <img
                          className="ico hide"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_packing3.gif"
                          alt="  40% "
                        />
                        <img
                          className="ico hide"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_salad.gif"
                          alt=""
                        />
                        <img
                          className="ico hide"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_yogurt.gif"
                          alt=""
                        />
                        <img
                          className="ico"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_parking.gif"
                          alt=""
                        />
                        <img
                          className="ico"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_delivery.gif"
                          alt=""
                        />
                        <img
                          className="ico hide"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_restaurant.gif"
                          alt=""
                        />
                        <img
                          className="ico hide"
                          src="//cdn.mrpizza.co.kr/2014_resources/images/store/ico_buffet.gif"
                          alt=""
                        />
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div className="f_right">
                <h2 className="cont_tit tit1">  </h2>
                <div className="st_tbl_style bggy">
                  <span>  .</span>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div>
            <span>  .</span>
          </div>
        )}
      </section>
    </div>
  );
};

export default Store;
